import { useQuery, useQueryClient } from '@tanstack/react-query'
import { useWalletUi } from '@wallet-ui/react'
import { JOURNAL_ENTRY_DISCRIMINATOR, getJournalEntryDecoder } from '@/generated' // 路径根据你的项目调整
import { Address } from '@solana/kit'

// 查询 Key
function useGetJournalEntriesQueryKey({ owner }: { owner: Address }) {
  const { cluster } = useWalletUi()
  return ['get-journal-entries', { cluster, owner }] as const
}

// 无效化查询
function useInvalidateGetJournalEntriesQuery({ owner }: { owner: Address }) {
  const queryClient = useQueryClient()
  const queryKey = useGetJournalEntriesQueryKey({ owner })
  return async () => {
    await queryClient.invalidateQueries({ queryKey })
  }
}

// 主查询 Hook
export function useGetJournalEntriesQuery({ owner }: { owner: Address }) {
  const { client } = useWalletUi()
  const queryKey = useGetJournalEntriesQueryKey({ owner })

  return useQuery({
    queryKey,
    queryFn: async () => {
      // 1. 使用 getProgramAccounts 查询所有 JournalEntry 账户
      const programAccounts = await client.rpc
        .getProgramAccounts('EQ84rMPQR8HQQ5QfHKyLnRUGF3XAaUMHtWmhv1ZzSEaE' as Address, {
          filters: [
            // 过滤 discriminator
            {
              memcmp: {
                offset: 0,
                bytes: Array.from(JOURNAL_ENTRY_DISCRIMINATOR)
                  .map((b) => b.toString(16).padStart(2, '0'))
                  .join(''),
              },
            },
            // 过滤 owner 字段（偏移 8）
            {
              memcmp: {
                offset: 8, // discriminator 之后就是 owner
                bytes: owner,
              },
            },
          ],
        })
        .send()

      // 2. 解码账户数据
      const decoder = getJournalEntryDecoder()
      return programAccounts.map((account) => {
        const decoded = decoder.decode(account.account.data)
        return {
          address: account.pubkey,
          ...decoded,
        }
      })
    },
  })
}

// 导出无效化函数（可选）
export { useInvalidateGetJournalEntriesQuery }
